/* logo */
.gradient-text-flag {
  background: linear-gradient(90deg, #e37f0c, #ffffff, #0c7e01);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}


/* text slider section for big screen */
.textSlider {

  animation: slideRight 30s linear infinite;
  animation-delay: 8s;
}

.textSliderfast {

  animation: slideRight 30s linear infinite;
  animation-delay: 4s;
}

.textSliderfaster {

  animation: slideRight 30s linear infinite;
}

/* Text sider section for small screen */
@media (max-width:476px) {
  .textSlider {

    animation: slideRightLeft 30s linear infinite;

  }

  .textSliderfast {

    animation: slideRightLeft 30s linear infinite;

  }

  .textSliderfaster {

    animation: slideRightLeft 30s linear infinite;
  }
}
/* medium screen */
@media (min-width:476px) and (max-width:767px) {
  .textSlider {

    animation: medium-screen 22s linear infinite;

  }

  .textSliderfast {

    animation: medium-screen 22s linear infinite;

  }

  .textSliderfaster {

    animation: medium-screen 22s linear infinite;
  }
}

/* Slider animation for large screen */

@keyframes slideRight {
  0% {
    transform: translateX(0%);
  }

  50% {
    transform: translate(38%);
  }

  100% {
    transform: translateX(0%);
  }
}

/* Sldier animation for small screen */
@keyframes slideRightLeft {
  0% {
    transform: translateX(-170%);
  }

  50% {
    transform: translateX(5%);
  }

  100% {
    transform: translateX(-170%);
  }

}
@keyframes medium-screen {
  0% {
    transform: translateX(-30%);
  }

  50% {
    transform: translateX(5%);
  }

  100% {
    transform: translateX(-30%);
  }

}

/* Easy navigion section  */
@media (max-width:476px) {

  .easy-navigation-button-small-screen {
    width: 42vw;
  }

}